import React from 'react';
import cc from 'classcat';
import { IconArtType, ThemeType } from './IconArt.types';
import './IconArt.css';

const ICON_ART_PATH = 'https://cdn.jsdelivr.net/gh/oriyan-ovadia/static@master/icons/lemonade-art';

export interface IconProps extends React.HTMLAttributes<HTMLImageElement> {
  icon: IconArtType;
  size?: 'sm' | 'md' | 'lg';
  theme?: ThemeType;
}

export const IconArt = ({ icon, size = 'md', theme = 'light', ...restProps }: IconProps) => {
  return (
    <span className={cc(['IconArt_root', `IconArt--${size}`])} {...restProps}>
      <img
        alt={`https://cdn.jsdelivr.net/gh/oriyan-ovadia/static@master/icons/lemonade-art/${icon}-art-${theme}.svg`}
        src=""
        {...restProps}
      />
    </span>
  );
};
